/* Clash between semantic ui and rc-tree-select */
.rc-tree-select-search__field {
    border: none !important;
}


/* Override the Windows xp like icons from rc-tree */
.rc-tree-select-tree-icon__close,
.rc-tree-select-tree-icon__open {
    background-image: url('data:image/png;base64,') !important;
    background-position: 0px;
}

.rc-tree-select-tree-icon__docu {
    background-image: url('data:image/png;base64,') !important;
    background-position: 0px;
}

.rc-tree-select-tree-node-content-wrapper {
    cursor: pointer;
}

.rc-tree-select-tree-treenode-disabled > span, .rc-tree-select-tree-treenode-disabled > a, .rc-tree-select-tree-treenode-disabled > a span {
    cursor: not-allowed;
    color: #888;
}

.formLabel-disabled {
    color:#aaa !important;
}